import React, { Component } from 'react'
import BootstrapTable from 'react-bootstrap-table-next';


export default class PharmacyInfoTab extends Component {

    constructor(props){
        super(props);
        this.state = {
            data : []
        }
    }

    componentWillReceiveProps(nextProps){ 
        if(nextProps.details.Prescriptions !== undefined){
            var templist = [];
            templist = nextProps.details.Prescriptions[0].PrescriptionDetail;
            this.setState({data : templist});
        }else{
            this.setState({data : []})
        }
    }

    componentDidMount = async () => {
        if(this.props.details.Prescriptions !== undefined){
            var templist = [];
            templist = this.props.details.Prescriptions[0].PrescriptionDetail;
            this.setState({data : templist});
        }
    }

    render(){
        const columns = [{
            dataField: 'PersonReviewedDate',
            text: 'Review Date'
          }, {
            dataField: 'PersonActive',
            text: 'Active?'
          }, {
            dataField: 'PersonIDNumber',
            text: 'ID',
            sort: true

          },{
            dataField: 'PersonDispenser',
            text: 'Dispenser'
          }, {
            dataField: 'PersonOther',
            text: 'Other'
          }, {
            dataField: 'SysLastEdited',
            text: 'SysLastEdited',

            
          },{
            dataField: 'PersonDateDeceased',
            text: 'DateDeceased'
          }, {
            dataField: 'PersonStatus',
            text: 'Status'
          }, {
            dataField: 'PersonPead',
            text: 'PersonPead',
            
            
          },{
            dataField: 'PersonDateJoined',
            text: 'DateJoined'
          }, {
            dataField: 'PersonNaive',
            text: 'PersonNaive'
          }, {
            dataField: 'PersonRegisteredInsys',
            text: 'PersonRegisteredInsys',
            
          }, {
            dataField: 'PersonInstitution',
            text: 'Institution',
            
          }, {
            dataField: 'PrescriberName',
            text: 'Name',
            
          }, {
            dataField: 'PrescriptionCost',
            text: 'PrescriptionCost',
            
          }
          ];
        return (

                <BootstrapTable 
                    striped
                    hover
                    keyField='PersonReviewedDate' 
                    data={ this.state.data } 
                    columns={ columns }/>
        );
    }
}